@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@1,600&family=Rock+Salt&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{margin:0;padding:0;box-sizing:border-box}body{background:linear-gradient(135deg, #667eea 0%, #764ba2 100%);color:white;font-family:"Poppins", sans-serif;min-height:100vh}header{font-size:2rem}h1{font-family:"Rock Salt", cursive}header,form{min-height:20vh;display:flex;justify-content:center;align-items:center}form input,form button{padding:0.5rem;font-size:2rem;border:none;background:white}form button{color:#8000ff;background:#f7fffe;cursor:pointer;transition:all 0.3s ease}form button:hover{background:#b700ff;color:white}.todo-container{display:flex;justify-content:center;align-items:center}.todo-list{min-width:30%;list-style:none}.todo{margin:0.5rem;background:white;font-size:1.5rem;color:black;display:flex;justify-content:space-between;align-items:center;transition:all 1s ease}.filter-todo{padding:1rem}.todo li{flex:1 1}.trash-btn,.complete-btn{background:#ff3700;color:white;border:none;padding:1rem;cursor:pointer;font-size:1rem}.complete-btn{background:#0bd4a2;transition:all 300ms}.complete-btn:hover{background:#09f}.trash-btn{transition:all 300ms}.trash-btn:hover{background:#b700ff}.todo-item{padding:0rem 0.5rem}.todo-input{width:40vw}.fa-trash,.fa-check{pointer-events:none}.fall{transform:translateY(10rem) rotateZ(20deg);opacity:0}.completed{text-decoration:line-through;opacity:0.6}.completed-right{background:#09f}select{-webkit-appearance:none;-moz-appearance:none;-ms-appearance:none;appearance:none;outline:0;box-shadow:none;border:0 !important;background-image:none}.select{margin:1rem;position:relative;overflow:hidden}select{color:#8000ff;font-family:"Poppins", sans-serif;cursor:pointer;width:12rem;height:53px}.select::after{content:"\25BC";position:absolute;top:0;right:0;padding:1rem;background:#8000ff;cursor:pointer;pointer-events:none;transition:all 0.5s}.select:hover::after{background:#b700ff}@media only screen and (max-width: 650px){.todo-input{width:50vw;font-size:16px;height:53px}.todo-button{height:53px}}
